const MY = {

	imgurl: '', // 图片拼接地址
	url: '/api', // 接口地址
	base_url: function() {

		return this.url








	},
	phone_type: uni.getSystemInfoSync().platform,
	tabh: function() {
		let num;
		let a = uni.getSystemInfo({
			success: (res) => {
				console.log(res, '264')
				let tabh1 = res.statusBarHeight
				let deviceWidth = uni.getSystemInfoSync().windowWidth;
				num = parseInt((750 / deviceWidth) * Number(tabh1)) + 'rpx';
				return num;
			},
			fail() {
				return "50rpx";
			}
		})
		return num
	},
	hidephone: function(phone) {
		if (!phone) return
		return phone.substring(0, 3) + "****" + phone.substring(7)
	},
	is_iOS: function() {
		if (uni.getSystemInfoSync().platform == 'ios') {
			return true
		}
		return false
	},
	time: function(time) {
		return new Date(parseInt(time)).getFullYear() + '年' + (new Date(parseInt(time)).getMonth() + 1) + '月' +
			new Date(parseInt(time)).getDate() + '日' + ' ' + (new Date(parseInt(time)).getHours() < 10 ? '0' :
				'') + new Date(parseInt(time)).getHours() + ':' + (new Date(parseInt(time)).getMinutes() < 10 ?
				'0' : '') + new Date(parseInt(time)).getMinutes()
	},
	timeymd: function(time) {
		return new Date(parseInt(time)).getFullYear() + '-' + (new Date(parseInt(time)).getMonth() < 9 ? '0' :
			'') + (new Date(parseInt(time)).getMonth() + 1) + '-' + (new Date(parseInt(time)).getDate() <
			10 ? '0' : '') + new Date(parseInt(time)).getDate()
	},
	post: function(url, datas) {
		// uni.showLoading()
		return new Promise(resolve => {
			console.log(this.url + url);
			uni.request({
				url: this.url + url,
				data: datas,
				method: 'POST',
				dataType: "json",
				header: {
					'token': uni.getStorageSync('token'),
					'Content-Type': 'application/x-www-form-urlencoded',
					'x-requested-with': 'XMLHttpRequest' //自定义请求头信息
				},
				success: ({
					data
				}) => {
					if (data.code === 204) uni.reLaunch({
						url: '/pages/login/login'
					})
					// this.toast('网络请求失败')
					resolve(data)
				},
				fail: () => {
					this.toast('网络请求失败')
				},
				complete: () => {
					// uni.hideLoading()
				}
			})
		})

	},
	api: function(url, data, method, text) {
		if (method == 'post') {
			return new Promise((resolve, reject) => {
				this.post(url, data).then(res => {
					console.log(res, text);
					resolve(res);
				})
			});
		} else {
			return new Promise((resolve, reject) => {
				this.get(url, data).then(res => {
					console.log(res, text);
					resolve(res)
				})
			});
		}
	},

	get: function(url, datas) {
		return new Promise(resolve => {
			uni.request({
				url: this.url + url,
				data: datas || {},
				method: 'GET',
				dataType: "json",
				header: {
					'token': uni.getStorageSync('token'),
					'Content-Type': 'application/x-www-form-urlencoded',
					'x-requested-with': 'XMLHttpRequest' //自定义请求头信息
				},
				success: ({
					data
				}) => {
					if (data.code === 204) uni.reLaunch({
						url: '/pages/login/login'
					})
					resolve(data)
				},
				fail: () => {
					this.toast('网络请求失败')
				},
				complete: () => {
					// uni.hideLoading()
				}
			})
		})
	},
	choosepic: function() {
		return new Promise(resolve => {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					uni.uploadFile({
						url: this.base_url() + 'member/upload_avatar',
						filePath: res.tempFilePaths[0],
						name: 'url',
						success: (e) => {
							let res = JSON.parse(e.data)
							if (res.code == 1000) {
								resolve(res.data.url)
							} else {
								this.$my.toast(res.message)
							}
						},
						fail: () => {
							this.$my.toast('')
						}
					})
				}
			});
		})
	},
	// 姓名隐藏名
	// 姓名隐藏名
	hideName(name) {
		if (!name) return;
		let name1 = name;
		if (name.length == 2) {
			name1 = name.substring(0, 1) + "*";
		} else if (name.length == 3) {
			name1 = name.substring(0, 1) + "**";
		} else if (name.length == 4) {
			name1 = name.substring(0, 1) + "***";
		} else if (name.length == 5) {
			name1 = name.substring(0, 1) + "****";
		}
		return name1;
	},
	hideIdcard(idcard) {
		if (!idcard) return;
		return idcard.substring(0, 6) + "******" + idcard.substring(13);
	},
	hidePhone: function(phone) {
		if (!phone) return;
		return phone.substring(0, 3) + "****" + phone.substring(7)
	},

	go(url) {
		if (!url) {
			uni.navigateBack({
				delta: 1
			})
		} else {
			uni.navigateTo({
				url
			})
		}

	},

	toast: function(msg) {

		uni.showToast({
			title: msg,
			icon: "none"
		})








	},
	//防抖
	dou: function() {
		let timer = null;
		return function(callback, delayed) {
			clearTimeout(timer)
			timer = setTimeout(() => {
				callback()
			}, delayed)
		}
	},
	//节流
	shollte: function() {
		let flag = true
		return function(callback, delayed) {
			if (flag) {
				callback()
				flag = false
				setTimeout(() => {
					flag = true
				}, delayed)
			}
		}
	}
}

export default MY
